import { useState, useEffect, useRef, createRef, RefObject } from 'react';

const mouseRef = createRef<HTMLDivElement>();

const useMousePointer = (withComponent = true) => {
  const mouseLocation = useRef({ x: 0, y: 0 });

  const isPointerInElement = (ref: RefObject<HTMLElement>) => {
    const object = ref.current;
    console.log({ object, mouseLocation });
    return true;
  };

  const handleOnMouseMove = (e: MouseEvent) => {
    const { clientX: x, clientY: y } = e;
    mouseRef.current.style.left = `${x}px`;
    mouseRef.current.style.top = `${y}px`;

    mouseLocation.current = {
      x,
      y,
    };
  };

  useEffect(() => {
    if (withComponent) {
      document.addEventListener('mousemove', handleOnMouseMove);
    }

    return () => {
      if (withComponent) {
        document.removeEventListener('mousemove', handleOnMouseMove);
      }
    };
  }, [element]);

  return { isPointerInElement, mouseLocation };
};

export default useMousePointer;
